:host {
  @apply flex items-center w-full;
}
:host ::ng-deep .p-inputtext {
  color: var(--form-field-color) !important;
  background: var(--p-inputtext-background) !important;
  border: 1px solid var(--p-inputtext-border-color) !important;
}
:host ::ng-deep .p-inputtext:enabled:focus {
  border-color: #378bc0 !important;
}

.select-client-wrapper {
  width: 100%;
  padding: 1rem;
}

.select-client-card {
  background-color: var(--surface-card);
  padding: 2rem;
  border-radius: var(--border-radius);
  box-shadow: var(--card-shadow);
  max-width: 550px;
  margin: auto;
}

.header {
  text-align: center;
  margin-bottom: 2rem;
}

.title {
  font-size: 1.75rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.subtitle {
  color: var(--text-color-secondary);
  font-size: 1rem;
}
